@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';






























































































































































































.q-banner {
  width: 100%;
  margin: 0 auto;
  top: 50px;
  z-index: 2;
  opacity: 0.8;
}
.localUser {
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  position: absolute;
  object-fit: cover;
  @media (max-width: $breakpoint-sm-max) {
    top: 0;
    height: 50vh;
    width: 100%;
  }
  @media (max-width: $breakpoint-xs-max) {
    width: 100%;
    height: 100%;
  }
}
.q-btn {
  margin-top: 80vh;
  z-index: 2;
}
.remoteUser {
  bottom: 0;
  right: 0;
  height: 100%;
  width: 50%;
  position: absolute;
  object-fit: cover;
  z-index: 1;
  background: $dark;
  @media (max-width: $breakpoint-sm-max) {
    bottom: 0;
    height: 50vh;
    width: 100%;
  }
  @media (max-width: $breakpoint-xs-max) {
    top: 0;
    margin: 0.9rem;
    height: 40vh;
    width: 50vw;
  }
}
